/* Reset styles */
html,body, body * { 
  box-sizing: border-box; 
  font-family: 'Open Sans', sans-serif; 
  margin:0;
  padding:0;
  color:inherit;
  background-color:inherit;
  /* border:2px solid orange; */
} 

.rectangle {
  display: inline-block;
  border: 3px double white;
  padding-top: 50px;
  padding-right: 55px;
  padding-bottom: 50px;
  padding-left: 95px;
  box-sizing: border-box;
  background: black;
  background-image: url(E3\ Logo.PNG);
  background-size:  cover;
  background-position:center center;
}
 
body { 
  background-image: url(E3.2_auto_x2.jpg);
  background-size:  cover;
  background-position:center center;
}
.container {
    width: 100%;
    padding-top: 60px;
    padding-bottom: 100px;
}


.login-register-wrapper {
    height: 620px;
    width: 500px;
    margin-left: auto;
    margin-right: auto;
    padding:0 2em;
    border-radius: 5px;
    box-shadow: 0px 2px 7px rgba(0,0,0,0.2);
    overflow: hidden; 
    color:#ffffff;
    background: linear-gradient(180deg, #090909 0%, #131313 52%, #202020 82%);
}

/* Nav buttons */
.nav-buttons {
  width: 100%;
  height: 100px;
  padding-top: 40px;
  opacity: 1;
  margin-bottom: 3em;
}
button {
    font-size: 1.5em;
    margin-right:1em;
    border:0px;
    cursor: pointer;
 
}
button:focus {
    outline: 0px;
}
button.active {
    padding-bottom: 10px;
    border-bottom: solid 2px #1059FF;
} 

 /* footer: forgot section */
 .forgot-panel {
  position: relative;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  text-align: center;
  padding-top: 24px;
  border-top: solid 1px rgba(255,255,255,.3);    
}

  /* form styling */
  .form-group input {
      width: 100%;
      height: 35px;
      padding-left: 15px;
      border: none;
      border-radius: 5px;
      margin-bottom: 20px;
      background: rgba(255,255,255,.5);
  }
  .form-group label {
    text-transform: uppercase;
  }
  input.submit {
      font-weight: 700;
      text-transform: uppercase;
      font-size: 1em;
      text-align: center;
      color: rgba(255,255,255, 1);
      padding: 8px 0px;
      width: 100%;
      height: 35px;
      border: none;
      border-radius: 20px;
      margin-top: 23px;
      background-color: rgb(0, 0, 0);
      cursor: pointer;
  }

  /*Form positioning and animation */

  form#registerform {
      left:500px;
      top:-230px;
      position:relative;
  }

  .forgot-panel {
      margin-top:-300px;
      color:gray;
  }

  form, .forgot-panel { 
      position: relative;
      transition: all .2s ease;
  }
  #loginBtn, #registerBtn {
      transition: all .2s ease;

  }
